import React, { useState } from 'react';
import {
  contact_form,
  contact_content,
  contact_form_container,
} from '../../Styles/contact_form.module.css';
import {
  ContactContainer,
  ContactImage,
  ContactFormHeader,
  FormFieldGroup,
  FormInputGroup,
  SubmitButton,
} from '../Branding';

const ContactForm = ({
  heading = 'Write Your Review',
  tag = 'OUR CONTACT',
  imageUrl = 'https://images.unsplash.com/photo-1573164713714-d95e436ab8d6?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1469&q=80',
}) => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    message: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
  };

  return (
    <section className={`${contact_form} container`}>
      <ContactContainer>
        <ContactFormHeader tag={tag} heading={heading} />
        <div className={contact_content}>
          <ContactImage src={imageUrl} />
          <div className={contact_form_container}>
            <form onSubmit={handleSubmit}>
              <FormFieldGroup>
                <FormInputGroup
                  id="firstname"
                  name="firstName"
                  label="First Name"
                  value={formData.firstName}
                  onChange={handleChange}
                />
                <FormInputGroup
                  id="lastname"
                  name="lastName"
                  label="Last Name"
                  value={formData.lastName}
                  onChange={handleChange}
                />
              </FormFieldGroup>

              <FormInputGroup
                id="email"
                name="email"
                label="Email"
                type="email"
                value={formData.email}
                onChange={handleChange}
              />

              <FormInputGroup
                id="message"
                name="message"
                label="Message"
                type="textarea"
                value={formData.message}
                onChange={handleChange}
              />

              <SubmitButton />
            </form>
          </div>
        </div>
      </ContactContainer>
    </section>
  );
};

export default ContactForm;
