import { ArrowRight } from 'lucide-react';
import React from 'react';
import {
  miniHero,
  miniHeroOverlay,
  miniHeroContent,
  breadcrumb,
  breadcrumbSeparator,
  breadcrumbLink,
  breadcrumbText,
} from '../../Styles/miniHero.module.css';

const MiniHero = ({ title, path }) => {
  return (
    <div className={miniHero}>
      <div className={miniHeroOverlay}></div>
      <div className={miniHeroContent}>
        <h1>{title}</h1>
        <div className={breadcrumb}>
          {path.map((item, index) => (
            <React.Fragment key={index}>
              {index > 0 && (
                <span className={breadcrumbSeparator}>
                  <ArrowRight />
                </span>
              )}
              {item.href ? (
                <a href={item.href} className={breadcrumbLink}>
                  {item.value}
                </a>
              ) : (
                <span className={breadcrumbText}>{item.value}</span>
              )}
            </React.Fragment>
          ))}
        </div>
      </div>
    </div>
  );
};

export default MiniHero;
