import React, { useRef, useState } from 'react';

import { Swiper, SwiperSlide } from 'swiper/react';


import 'swiper/css';
import 'swiper/css/pagination';

import './styles.css';


import { Pagination, Navigation, Autoplay } from "swiper/modules";


import {
  
  testimonialText,
  testimonialAuthor,
  quoteMark,
  authorImage,
  authorInfo,
  testimonialCard
} from '../../Styles/testimonials.module.css';

export default function SliderTestimonials({testimonials}) {
  return (
    <>
      <Swiper slidesPerView={1} loop={true}
              pagination={{ clickable: true }}
              navigation={true}
              autoplay={{
                delay: 6000,
                disableOnInteraction: false,
              }}
              modules={[Navigation, Pagination,Autoplay]} className="testimonial_swiper">
        
       {
        testimonials.map((testimonial,index)=>{
            return  <SwiperSlide key={index}> <div  className={testimonialCard}>
            <img
              className={quoteMark}
              src="https://askproject.net/finest/wp-content/uploads/sites/105/2022/10/quote_Asset-4.png"
              alt=""
            />
    
            <p className={testimonialText}>{testimonial.text}</p>
            <div className={testimonialAuthor}>
              <img
                src={testimonial.image}
                alt={testimonial.author}
                className={authorImage}
              />
              <div className={authorInfo}>
                <h4>{testimonial.author}</h4>
                <p>{testimonial.position}</p>
              </div>
            </div>
          </div></SwiperSlide>
        })
       }
        
      </Swiper>
      <div className="swiper-pagination-custom custom-pagination"></div>
    </>
  );
}
